@import url(https://fonts.googleapis.com/css2?family=Do+Hyeon&display=swap);
html * {
  font-family: 'Do Hyeon', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container-fluid {
  margin: auto;
}
.form-label {
    font-size: 14px;
    color: #777777;
}

input.form-control {
    text-align: center;
}

#input-form {
    max-width: 500px;
    margin: auto;
    padding: 40px 0;
}
.pill-checkbox {
    margin: 10px;
    padding: 7px 12px;
    border-radius: 25px;
    border: solid 1px;
}

.pill-checkbox input[type="checkbox"] {
    opacity: 0;
}

.pill-label {
    font-size: 14px;
    color: #14a4be;
}

.pill-checkbox .checked {
    color: #ffffff;
}
